
import * as d3 from 'd3';
import $ from "jquery";
import blah from 'typeahead.js'

// Local imports
import { createCircleChart } from './circle';  // named import
import makeBarChart from './bar';  // name is irrelevant since it is a default export
import Bins from './Bins'

require('./main.scss');  // will build CSS from SASS 3 file

// eslint-disable-next-line no-unused-vars
function buildCircleChart(chartWidth, chartHeight) {
  // Dataset
  const dataset = d3.range(20);

  const svg = d3.select('#viz')
                .append('svg')
                .attr('preserveAspectRatio', 'xMinYMin meet')
                .attr('viewBox', `0 0 ${chartWidth} ${chartHeight}`)
                .classed('svg-content-responsive', true);

  // Test module, string interpolation
  return createCircleChart(svg, dataset);
}

// Main
const vizDiv = document.getElementById("viz");

const chartWidth = vizDiv.clientWidth;
const chartHeight = vizDiv.clientHeight;
// d3.select('#viz').remove();

d3.json("player_matricies.json", function(playerData) {
	d3.json("metric_matricies.json", function(metricData) {
	  	const bin = new Bins('viz', playerData, metricData)

	  	const NBA = require("nba");

	  var substringMatcher = function(strs) {
	    return function findMatches(q, cb) {

	      var matches, substringRegex;

	      // an array that will be populated with substring matches
	      matches = [];

	      // regex used to determine if a string contains the substring `q`
	      substringRegex = new RegExp(q, 'i');


	      // iterate through the pool of strings and for any string that
	      // contains the substring `q`, add it to the `matches` array
	      $.each(strs, function(i, str) {
	      if (substringRegex.test(str)) {
	        matches.push(str);
	      }
	    });
	      cb(matches);
	    };
	  };


	  $('#submit').click(function() {
	    // Get value of input
	    var name = $('input.typeahead.tt-input').val();
	    bin.changePlayer(name)
	    var both_names = name.split(' ')
	    var img = $('<img />', {
	    	src : 'https://nba-players.herokuapp.com/players/' + both_names[1] +'/' + both_names[0]
	    });
    	$("#head img:first-child").remove()
    	$('#head').append(img)
	    const player = NBA.findPlayer(name);
	    d3.select('#bio').selectAll('.datarow')
					.data([])
					.exit()
					.remove()
	    if(!player) {
	    	return
	    }
		//commonPlayerInfo
		NBA.stats.playerProfile({ PlayerID: player.playerId }).then(function(profileData) {
			NBA.stats.playerInfo({ PlayerID: player.playerId }).then(function(infoData){
				var stats = d3.select('#bio').selectAll('.datarow')
					.data(profileData.seasonTotalsRegularSeason)
					.enter()
					.append('tr');
				stats.classed('datarow', true)
				stats.append('td')
					.html((d) => d.seasonId)
					.classed('cell seasonId', true)
				stats.append('td')
					.html((d) => d.teamAbbreviation)
					.classed('cell team', true)
				stats.append('td')
					.html((d) => d.playerAge)
					.classed('cell playerAge', true)
				stats.append('td')
					.html((d) => d.gp)
					.classed('cell gp', true)
				stats.append('td')
					.html((d) => d.min)
					.classed('cell min', true)
				stats.append('td')
					.html((d) => d.pts)
					.classed('cell pts', true)
				stats.append('td')
					.html((d) => d.reb)
					.classed('cell reb', true)
				stats.append('td')
					.html((d) => d.ast)
					.classed('cell ast', true)
				stats.append('td')
					.html((d) => d.blk)
					.classed('cell blk', true)
				stats.append('td')
					.html((d) => d.stl)
					.classed('cell stl', true)
				stats.append('td')
					.html((d) => d.tov)
					.classed('cell tov', true)
				stats.append('td')
					.html((d) => d.fgPct)
					.classed('cell fgPct', true)
				stats.append('td')
					.html((d) => d.ftPct)
					.classed('cell ftPct', true)
				stats.append('td')
					.html((d) => d.fg3Pct)
					.classed('cell fg3Pct', true)
				console.log(profileData)
				console.log(infoData)
			});
		});
		NBA.stats.playerInfo({ PlayerID: player.playerId }).then(function(data){

		});
	  });

	  $('#stat').change(function(e) {
	    bin.changeStatType(e.target.value)
	  })

	  $('.typeahead').typeahead({
	    hint: true,
	    highlight: true,
	    minLength: 1,
	  },
	  {
	    name: 'players',
	    source: substringMatcher(Object.keys(playerData))
	  });
	});
});


