import * as d3 from 'd3';
import * as colors from 'd3-scale-chromatic';

let playerData
const MARGIN = { top: 20, right: 30, bottom: 20, left: 30 };
const PADDING = { top: 60, right: 60, bottom: 60, left: 60 };
const OUTER_WIDTH = 825;
const OUTER_HEIGHT = 500;


export default class Bins {
	constructor(id, playerData, metricData, outerWidth = OUTER_WIDTH, outerHeight = OUTER_HEIGHT) {
		this.id = id;
		this.metricData = metricData
		this.rawData = playerData
		this.baseWidth = outerWidth
		this.baseHeight = outerHeight
		this.svg = d3.select(`#${this.id} svg`);
		this.statType = 'raw'

    	// If empty create element
		if (this.svg.empty()) {
			this.svg = d3.select('body')
				.insert('div')
					.attr('id', this.id)
				.append('svg')
					.attr('width', this.baseWidth)
					.attr('height', this.baseHeight);
    	}

		const inner = this.svg.append('g')
			.attr('transform', `translate(${MARGIN.left+40}, ${MARGIN.top})`)
    		.attr('class', 'inner');

		// [ {playerDist, baketDist, madeValue, attValue}, ... ]
		// playerDist = [0-20]

		this.playerDistScale = d3.scaleLinear()
			.domain([0, playerData['Kyle Korver']['made'].length])
			.range([this.getHeight(), 0]);

		this.basketDistScale = d3.scaleLinear()
			.domain([0, playerData['Kyle Korver']['made'][0].length*2])
			.range([0, this.getWidth()]);

		this.color = (x) => colors.interpolateWarm(x);


		// Make bins
	    inner.append('g')
	       .attr('class', 'bins');

	    // Make axis
	    inner.append('g')
	        .attr('class', 'labels');


	    // Make axis
	    const axes = inner.append('g')
	                      .attr('class', 'axes');
	    axes.append('g') // x axis
	        .attr('class', 'axis')
	        .attr('class', 'xaxis');
	    axes.append('g')  // y axis
	        .attr('class', 'axis')
	        .attr('class', 'yaxis');


	    this.setAxes(this.basketDistScale, this.playerDistScale)

	}

	changePlayer(name) {
		if (name in this.rawData) {
			this.maxAtt = 0;
			this.maxDiff = 0;
	  		let data = this.rawData[name];
	  		this.data = []
			for (let playerDist in data['made']) {
				for (let basketDist in data['made'][playerDist]) {
					const madeValue = data['made'][playerDist][basketDist];
					const attValue = data['att'][playerDist][basketDist];
					if (attValue > this.maxAtt)
						this.maxAtt = attValue
					let diff = 1.0*madeValue/attValue - this.metricData[playerDist][basketDist]
					if (diff > this.maxDiff)
						this.maxDiff = diff
					this.data = [...this.data, {playerDist: parseInt(playerDist), basketDist: parseInt(basketDist), madeValue, attValue}]
				}
			}
			this.render()
		}
		else {
			window.alert("That is not the name of an NBA player!");
		}
  	}

  	changeStatType(statType) {
  		this.statType = statType
  		const yScale = this.playerDistScale;
	    const xScale = this.basketDistScale;
  		const bins = this.svg.select('g.inner g.bins')
	                          .selectAll('rect')
	                          .data(this.data);
	    this.setBins(bins.transition(), xScale, yScale, statType)
  	}

	render() {
		const yScale = this.playerDistScale;
	    const xScale = this.basketDistScale;
	    // Set bars
	    const bins = this.svg.select('g.inner g.bins')
	                          .selectAll('rect')
	                          .data(this.data);
	    this.setBins(bins.enter().append('rect'), xScale, yScale);

	    // Set labels
	    const labels = this.svg.select('g.inner g.labels')
	                         .selectAll('text')
	                         .data(this.data);
	    // Bins.setLabels(labels.enter().append('text'), xScale, yScale);

	    this.setBins(bins, xScale, yScale);

	}



	setAxes(xScale, yScale) {
	    const yAxis = d3.axisLeft(yScale);
	    const xAxis = d3.axisBottom(xScale)

	    this.svg.select('g.inner g.axes g.xaxis')
	            .attr('transform', `translate(0, ${this.getHeight()})`)
	            .call(xAxis);
	    this.svg.select('g.inner g.axes g.yaxis')
	            .call(yAxis);

	    this.svg.append("text")
		      .attr("transform", "rotate(-90)")
		      .attr("y", 50 - MARGIN.left)
		      .attr("x", 50 - (OUTER_HEIGHT / 2))
		      .attr("dy", "1em")
		      .style("text-anchor", "middle")
		      .style('fill', 'white')
		      .text("Shooter Distance from Nearest Defender (ft)");


		this.svg.append("text")
		      .attr("transform",
		            "translate(" + (OUTER_WIDTH/2 - 30) + " ," +
		                           (OUTER_HEIGHT + MARGIN.top - 120) + ")")
		      .style("text-anchor", "middle")
		      .style('fill', 'white')
		      .text("Shooter Distance from Basket (ft)");

	}




	getWidth() {
	    const outerWidth = parseInt(this.svg.style('width'), 10);
	    const innerWidth = outerWidth - MARGIN.left - MARGIN.right;
	    const width = innerWidth - PADDING.left - PADDING.right;

	    return width;
	}

	getHeight() {
	    const outerHeight = parseInt(this.svg.style('height'), 10);
	    const innerHeight = outerHeight - MARGIN.top - MARGIN.bottom;
	    const height = innerHeight - PADDING.top - PADDING.bottom;

	    return height;
	}


	setBins(sel, xScale, yScale, statType = this.statType) {
		let yMax = this.rawData['Kyle Korver']['made'].length
		// Define the div for the tooltip
		var div = d3.select("body").append("div")
		    .attr("class", "tooltip")
		    .style("opacity", 0);
	    sel.attr('x', d => {
	    		return (xScale(2*d.basketDist) + (xScale(2)-xScale(2*Math.sqrt(d.attValue/this.maxAtt)))/2) || 0
	    	})
	       .attr('y', d => {
	       		return (yScale(d.playerDist) - yScale(yMax-1) + (yScale(yMax-1)-(yScale(0)-yScale(Math.sqrt(d.attValue/this.maxAtt))))/2) || 0
	       })
	       .attr('width', d => {
		       	return (xScale(2*Math.sqrt(d.attValue/this.maxAtt))) || 0
	       })
	       .attr('height', d => {
	       		return (yScale(0)-yScale(Math.sqrt(d.attValue/this.maxAtt))) || 0
	       })
		   .style("fill", d => {
		   		if (statType == 'raw') {
		   			return this.color(1.0 * d.madeValue / (d.attValue || 10000000))
		   		}
		   		else if (statType == 'metric') {
		   			// done
		   			let modelProb = this.metricData[d.playerDist][d.basketDist]
		   			let madePercent = 1.0*d.madeValue/(d.attValue||1000000000)
		   			if (madePercent != 0)
		   				return this.color(madePercent-modelProb+.5)
		   			else
		   				return this.color(0)
		   		}
		   	})
		   .on("mouseover", function(d) {
		   	div.transition()
                .duration(200)
                .style("opacity", .9);
            div.html("made: " + d.madeValue + "<br/>"  + "att: " + d.attValue)
                .style("left", (d3.event.pageX) + "px")
                .style("top", (d3.event.pageY - 28) + "px");
            })
		   .on("mouseout", function(d) {
            div.transition()
                .duration(500)
                .style("opacity", 0);
	        });

  	}
}



